<template>
  <div v-if="config">
    <div v-if="config.viewName">
      <ld-header>{{ config.viewName }}</ld-header>
    </div>
    <div v-if="config.viewType">
      <component :is="`view-${config.viewType.toLowerCase()}`" :config="config"></component>
    </div>
    <div>
      <ld-bottom></ld-bottom>
    </div>
  </div>
</template>
<script>
/**
 * @file 渲染盒子
 */
import Header from '../../components/Header';
import Bottom from '../../components/Bottom';
import ViewA from '../../view/A';
import ViewB from '../../view/B';
import service from '../../service';
import ld from '../../utils/ld';

export default {
  data() {
    return {
      config: null,
    }
  },
  mounted() {
    this.loadData();
  },
  watch: {
    $route() {
      this.loadData();
    }
  },
  methods: {
    loadData() {
      const { id } = ld.getUrlParams();
      const config = service.getMockData(id);
      this.config = config;
    }
  },
  components: {
    'view-a': ViewA,
    'view-b': ViewB,
    'ld-header': Header,
    'ld-bottom': Bottom,
  }
}
</script>


