<template>
  <div>
    <div>
      <ld-header>登记票据</ld-header>
    </div>
    <div>
      <view-a :config="config" :tableData="tableData"></view-a>
    </div>
    <div>
      <ld-bottom></ld-bottom>
    </div>
  </div>
</template>
<script>
import Header from '../../components/Header';
import Bottom from '../../components/Bottom';
import ViewA from '../../view/A';

const config = {
  viewId: 'view3', // 界面编号
  viewName: '票据种类界面', // 界面名称
  getId: '', // 取数ID
  // fieldList: ['piaoju', 'zhangshu', 'jine'], // 显示字段列表
  // fieldTitle: ['票据', '张数', '金额'], // 字段名
  // inputType: 1, // 录入类型 0 无输入 1 表格
  fieldConfig: [
    {
      fieldName: 'piaoju',
      fieldTitle: '票据',
      inputType: 1,
    },
    {
      fieldName: 'zhangshu',
      fieldTitle: '张数',
      inputType: 1,
    },
    {
      fieldName: 'jine',
      fieldTitle: '金额',
      inputType: 1,
    },
  ],
  nextViewId: 'view4', // 跳转的表格界面ID
};

export default {
  data() {
    return {
      config,
      tableData: [
        {
          piaoju: '过路过桥费'
        },
        {
          piaoju: '燃油销售发票'
        },
        {
          piaoju: '对应票据'
        }
      ],
    };
  },
  components: {
    'view-a': ViewA,
    'ld-header': Header,
    'ld-bottom': Bottom,
  }
};
</script>



