<template>
  <div>
    <table class="ld-table">
      <tr class="th row">
        <th v-if="editMode" style="width:5rem;">
        </th>
        <th
          class="row-item"
          :class="{ 'td-border': showBorder }"
          v-for="col in columns"
          :key="col[0]"
        >
          {{ col.fieldTitle || col.fieldName }}
        </th>
      </tr>
      <tr
        class="tr row"
        v-for="(item, rowIdx) in data"
        :key="rowIdx"
      >
        <td v-if="editMode" class="row-order" @touchend="onDelLine">
          {{ rowIdx + 1 }}
        </td>
        <td
          class="td row-item"
          :class="{ 'td-border': showBorder }"
          v-for="(col, colIdx) in columns"
          :key="'td' + col.fieldName"
          @touchend="onClickItem(item, rowIdx, colIdx)"
        >
          <template v-if="col.inputType == 1">
            <!-- 表格 -->
            {{ item[col.fieldName] }}
          </template>
          <template v-else-if="col.inputType == 2">
            <!-- 文本 -->
            <input type="text" v-model="item[col.fieldName]">
          </template>
          <template v-else-if="col.inputType == 3">
            <!-- 数字 -->
            <input type="number" v-model="item[col.fieldName]">
          </template>
          <template v-else-if="col.inputType == 4">
            <!-- 金额 -->
            <input type="number" v-model="item[col.fieldName]">
          </template>
          <template v-else-if="col.inputType == 5">
            <!-- 日期 -->
          </template>
          <template v-else-if="col.inputType == 6">
            <!-- 编码 -->
          </template>
          <template v-else>
            <!-- 无输入 -->
            {{ item[col.fieldName] }}
          </template>
        </td>
      </tr>
    </table>
    <div v-if="editMode" class="icon-plus" @touchend="onAdd"></div>
  </div>
</template>
<script>
import { clone } from '@/utils';
import '@/components/Dialog';

/**
 * 1. 文档说明
 * 2. 注释
 * 3. 字段名字
 */

export default {
  data() {
    return {
      modal: {
        show: false,
        text: '',
      },
      editConf: {
        row: -1,
        col: -1,
      },
    };
  },
  props: {
    data: {
      type: Array,
      default() {
        return [{}];
      },
    },
    /**
     * [{ fieldName: 字段名, title: 中文名, editable: 是否可编辑 }, ...]
     */
    columns: {
      type: Array,
      default() {
        return [];
      },
    },
    border: Boolean, // 竖线的边框
    editMode: Boolean, // 编辑状态的表格
  },
  computed: {
    showBorder() {
      return this.border || this.editMode;
    },
    // 新加一行的默认数据
    newRowVal() {
      const val = {};
      this.columns.forEach(o => {
        val[o.fieldName] = null;
      });
      return val;
    }
  },
  watch: {
    data(v) {
      const { row, col } = this.editConf;
      if (this.editMode && (row > -1 && col > -1)) {
        this.$emit('onInput', {
          data: v,
          row,
          col,
        });
      }
    }
  },
  methods: {
    // 点击单元格
    onClickItem(item, row, col) {
      this.$emit('clickItem', item, row, col);
      this.editConf.row = row;
      this.editConf.col = col;
    },
    onAdd() {
      this.data.push(clone(this.newRowVal));
    },
    async onDelLine() {
      try {
        if (this.data.length < 1) {
          return;
        }
        await this.$dialog({
          text: '是否删除？',
          cancelHighlight: true,
        });
        this.data.pop();
      } catch (_) {}
    }
  },
};
</script>
<style lang="scss">
$borderColor: #ccc;

.ld-table {
  margin-top: 2rem;
}
.ld-table th {
  font-weight: normal;
}
.ld-table input {
  text-align: right;
}

.ld-table {
  width: 100%;
  font-size: 1.6rem;
  border-collapse: collapse;
  table-layout: fixed;
}
.ld-table .th.row {
  background: #f7f7f7;
  line-height: 1.5em;
  text-align: center;
  border: solid $borderColor;
  border-width: 1px 0;
}
.ld-table .tr.row {
  height: 2em;
  line-height: 2em;
  text-align: center;
  border-bottom: 1px solid $borderColor;
}
.ld-table .td-border {
  border: 1px solid $borderColor;
}
.ld-table .row-order {
  width: 5rem;
  position: relative;
  &::before {
    content: 'x';
    font-size: 1.6rem;
    position: absolute;
    right: 0;
    top: 0;
    line-height: 1.6rem;
  }
}
.ld-table .td input {
  width: 100%;
}

.icon-plus {
  width: 5rem;
  height: 5rem;
  margin: 0 auto;
  background: url('../assets/img/add.png') no-repeat 50% 50% transparent;
  background-size: 1.6rem 1.6rem;
}
</style>



