<template>
  <div>
    <div>
      <ld-header>登记票据明细</ld-header>
    </div>
    <div>
      <view-a :config="config" :tableData="tableData"></view-a>
    </div>
    <div>
      <ld-bottom></ld-bottom>
    </div>
  </div>
</template>
<script>
import Header from '../../components/Header';
import Bottom from '../../components/Bottom';
import ViewA from '../../view/A';

export default {
  data() {
    return {
      config: {
        viewId: 'view3', // 界面编号
        viewName: '登记票据明细', // 界面名称
        getId: '', // 取数ID
        // fieldList: ['piaoju', 'zhangshu', 'jine'], // 显示字段列表
        // fieldTitle: ['票据', '张数', '金额'], // 字段名
        // inputType: 1, // 录入类型 0 无输入 1 表格
        fieldConfig: [
          {
            fieldName: 'zhangshu',
            fieldTitle: '张数',
            inputType: 3,
          },
          {
            fieldName: 'danjia',
            fieldTitle: '单价',
            inputType: 4,
          },
          {
            fieldName: 'heji',
            fieldTitle: '合计',
            inputType: 0,
          },
          {
            fieldName: 'beizhu',
            fieldTitle: '备注',
            inputType: 2,
          },
        ],
        nextViewId: 'view4', // 跳转的表格界面ID

        // 可编辑
        canEdit: true,
      },
      tableData: [{}, {}, {}],
    }
  },
  components: {
    'view-a': ViewA,
    'ld-header': Header,
    'ld-bottom': Bottom,
  }
}
</script>


