<template>
  <div class="list">
    <div
      class="bx-item ld-border bottom"
      v-for="item in config.tableData"
      @touchend="onClick">
      {{ item.name }}
    </div>
  </div>
</template>
<script>
/**
 * 传入 config
 * config.tableData为表格数据。tableData中每个元素的name属性用于展示
 */
import ld from '../utils/ld';

export default {
  props: ['config'],
  methods: {
    onClick() {
      ld.navigateTo({ viewId: this.config.nextViewId });
    }
  },
}
</script>
<style>
.bx-item {
  padding: 0 0.8rem;
  line-height: 4.8rem;
  display: flex;
  justify-content: space-between;
}
</style>


