import router from '../router';

export default {
  /**
   * 根据传入的id跳转界面
   *
   * @param {Object} options 跳转页面
   * @param {string} options.viewId 界面id
   */
  navigateTo(options) {
    const { viewId } = options;
    router.push({ query: { id: viewId }});
  },

  /**
   * 返回上一个页面
   */
  navigateBack() {
    router.go(-1);
  },

  /**
   * 获取url参数
   *
   * @return {Object} url参数，对象结构
   */
  getUrlParams() {
    return router.currentRoute.query;
  },

  /**
   * 设置网页标题
   * @param {string} title 副标题
   */
  initTitle(subTitle) {
    const prefix = '联道OA';
    document.title = prefix + (subTitle ? `\\${subTitle}` : '');
  }
};



