
/**
 * 判断是否为微信浏览器
 * @return {Boolean}
 */
export function isWeixin() {
  // 判断微信内置浏览器方法实例
  const ua = navigator.userAgent.toLowerCase();
  if (ua.match(/MicroMessenger/i) === 'micromessenger') {
    return true;
  }

  return false;
}

