<template>
  <ld-table
    :data="config.tableData" :columns="config.fieldConfig" @clickItem="onClickItem"
    border
    :editMode="config.canEdit"
  ></ld-table>
</template>
<script>
import ld from '../utils/ld';
import Table from '../components/Table';

const typeMap = {
  0: '无输入',
  1: '表格',
  2: '文本',
  3: '数字',
  4: '金额',
  5: '日期',
  6: '编码',
};

export default {
  components: {
    'ld-table': Table,
  },
  props: ['tableData', 'config'],
  methods: {
    onClickItem(item, colIdx) {
      const col = this.config.fieldConfig[colIdx];
      if (col.inputType == 1) {
        ld.navigateTo({
          viewId: this.config.identifyValue,
        });
      }
    }
  }
};
</script>

<style scoped>
</style>



